.footer-nav {
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;

  @include media-breakpoint-up(lg) {
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    text-align: start;
  }
}

.footer-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-nav__list:not(:last-child) {
  margin-bottom: 34px;

  @include media-breakpoint-up(lg) {
    margin-bottom: 0;
  }
}

.footer-nav__item {
  @include media-breakpoint-up(lg) {
    line-height: 22px;
  }
}

.footer-nav__item:not(:last-child) {
  margin-bottom: 2px;

  @include media-breakpoint-up(lg) {
    margin-bottom: 8px;
  }
}

.footer-nav__link {
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  color: $grey-light;

  @include color-transition;

  &:hover {
    color: $blue;
  }

  &:active {
    color: $blue-dark;
  }

  @include media-breakpoint-up(lg) {
    font-size: 16px;
    line-height: 22px;
  }
}
