.heading-primary {
  font-family: "Jost", "Arial", sans-serif;
  font-size: 30px;
  line-height: 39px;
  font-weight: 700;
  letter-spacing: 0;
  color: $black-1;

  @include media-breakpoint-up(md) {
    font-size: 40px;
    line-height: 52px;
  }

  @include media-breakpoint-up(lg) {
    font-size: 60px;
    line-height: 78px;
  }
}
